<template>
  <table>
    <thead>
      <tr>
        <th>userid</th>
        <th>bookname</th>
        <th>borrowtime</th>
        <th>deadline</th>
        <th>returntime</th>
        <th>fineamount</th>
        <th>Action</th> <!-- 新增列 -->
      </tr>
    </thead>
    <tbody>
      <tr v-for="fineinfo in displayinfo" :key="fineinfo.borrowid">
        <td>{{ fineinfo.userid }}</td>
        <td>{{ fineinfo.bookname }}</td>
        <td>{{ fineinfo.borrowtime }}</td>
        <td>{{ fineinfo.deadline }}</td>
        <td>{{ fineinfo.returntime }}</td>
        <td>{{ fineinfo.fineamount }}</td>
        <td>
          <!-- 新增按钮和点击事件 -->
          <button @click="redirectToPayment(fineinfo)">Pay</button>
        </td>
      </tr>
    </tbody>
  </table>
</template>

<script>
import fineinfo from "@/api/fineinfo";
export default {
  data() {
    return {
      fineinfos: "",
    };
  },
  mounted() {
    fineinfo.fineinfo().then((res) => {
      console.log(res.data);
      this.fineinfos = res.data;
    });
  },
  computed: {
    displayinfo() {
      return this.fineinfos.slice();
    },
  },
  methods: {
    redirectToPayment(fineinfo) {
      const { borrowid, bookname, fineamount } = fineinfo;
      const subject = encodeURIComponent(bookname);
      const traceNo = borrowid;
      const totalAmount = fineamount;

      // 构造跳转链接
      const paymentURL = `http://localhost:8080/alipay/pay?subject=${subject}&traceNo=${traceNo}&totalAmount=${totalAmount}`;

      // 执行跳转
      window.location.href = paymentURL;
    },
  },
};
</script>

<style>
table {
  border-collapse: collapse; /* 合并表格边框 */
  width: 100%; /* 表格宽度 */
}

th,
td {
  padding: 8px; /* 单元格内边距，根据需要进行调整 */
  text-align: center;
}
</style>
