<template>
  <table>
    <thead>
      <tr>
        <th>userid</th>
        <th>bookname</th>
        <th>borrowtime</th>
        <th>deadline</th>
        <th>status</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="borrowhistory in displayinfo" :key="borrowhistory.borrowtime">
        <!-- <td>{{ fineinfo.borrowid }}</td> -->
        <td>{{ borrowhistory.userid }}</td>
        <td>{{ borrowhistory.bookname }}</td>
        <td>{{ borrowhistory.borrowtime }}</td>
        <td>{{ borrowhistory.deadline }}</td>
        <td>{{ borrowhistory.status }}</td>
      </tr>
    </tbody>
  </table>
</template>

<script>
import borrowhistory from "@/api/borrowhistory";
export default {
  data() {
    return {
      borrowhistorys: "",
    };
  },
  mounted() {
    borrowhistory.borrowhistory().then((res) => {
      console.log(res.data);
      this.borrowhistorys = res.data;
    });
  },
  computed: {
    displayinfo() {
      return this.borrowhistorys.slice();
    },
  },
};
</script>

<style>
table {
  border-collapse: collapse;
  /* 合并表格边框 */
  width: 100%;
  /* 表格宽度 */
}

th,
td {
  padding: 8px;
  /* 单元格内边距，根据需要进行调整 */
  text-align: center;
}
</style>
