import axios from "axios";
import base from "./base";

const api = {
  remainBook() {
    return axios.get(`${base.baseURL}/book/remain`);
  },

  //获取剩余图书数量
  gettotalbooknum() {
    return axios.get(`${base.baseURL}/book/total`);
  },
  gettotalpatron(){
    return axios.get(`${base.baseURL}/patron/total`);
  },
  gettotalfine(){
    return axios.get(`${base.baseURL}/fine/total`);
  },
  gettotalpaid(){
    return axios.get(`${base.baseURL}/fine/paidTotal`);
  }
}
export default api;